
.uploaded-images-container {
	margin: auto;
	width: 85%;
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: -10px;
	height: fit-content;
  }
  .uploaded-images-container .header {
	width: fit-content;
	padding: 0 20px;
	background: #03316d;
  }
  
  .uploaded-images-container .header span {
	display: inline-block;
	height: 100%;
	padding: 10px;
	cursor: pointer;
	text-align: center;
  }
  
  .uploaded-images-container .header span:hover {
	background: #03254e;
  }
  
  .image-container {
	background: #fff;
	color: black;
	padding: 10px;
	display: flex;
	padding: 10px;
	flex-wrap: wrap;
	height: 70vh;
	overflow: scroll;
	/* justify-content: center; */
  }
  
  .image-item {
	border: 1px solid #e8e8e8;
	border-radius: 5px;
	font-size: 30px;
	text-align: center;
	height: 220px;
	width: 220px;
	/* flex: 1 0 21%; */
	margin: 5px;
  }
  .image-item .image {
	/* background-image: url("fabric_image.png");
	background-size: cover;
	background-position: center center; */
	width: 100%;
	position: relative;
	height: 80%;
  }
  .image-item .image img {
	width: 100%;
	height: 100%;
  }
  
  .image-item .image .stain-label {
	position: absolute;
	left: 0;
	top: 10px;
	background: #0f5820;
	font-size: 20px;
	color: white;
	padding: 5px 15px;
  }
  
  .image-item .footer {
	height: 20%;
	font-family: Arial, sans-serif;
	font-size: 12px;
	color: #5f5f5f;
	font-weight: 500;
	font-weight: bold;
	display: flex;
	align-items: center;
	padding: 15px;
  }
  .image-item .footer .img-icon {
	background-image: url("../../assets/img_icon.png");
	width: 20px;
	height: 20px;
	margin-right: 20px;
	background-size: cover;
	background-position: center center;
  }
  