@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@500&display=swap);
* {
  box-sizing: border-box;
  font-family: "Rajdhani", sans-serif;
}
body {
  margin: 0;
  color: #fff;
  background: linear-gradient(
    0deg,
    rgba(2, 38, 88, 1) 22%,
    rgba(3, 57, 129, 1) 60%
  );
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 800;
}

.page-container {
  display: grid;
  grid-gap: 1px;
  grid-template-rows: auto 20%;
  min-height: 100vh;
  position: relative;
}
.title {
  margin: 0 0 35px 0;
}

.page-item {
  text-align: center;
  padding: 20px;
  font-size: 30px;
}

/* upload screen / start analysis */
.upload-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 70%;
}

.upload-container .upload-item {
  margin: 15px;
}

.upload-container .file-upload,
.upload-container .batch-upload {
  border-radius: 10px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url(/nift-react/static/media/photo_bg.52e5b040.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30%;
}

.upload-container .file-upload {
  border: 1px dashed grey;
}
.upload-container .batch-upload {
  border: 1px solid grey;
}

.upload-container .upload-btn {
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  color: white;
  background: #ed3d6c;
  font-size: 25px;
  padding: 0;
  margin-top: 1rem;
  cursor: pointer;
}


.uploaded-images-container {
	margin: auto;
	width: 85%;
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: -10px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
  }
  .uploaded-images-container .header {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	padding: 0 20px;
	background: #03316d;
  }
  
  .uploaded-images-container .header span {
	display: inline-block;
	height: 100%;
	padding: 10px;
	cursor: pointer;
	text-align: center;
  }
  
  .uploaded-images-container .header span:hover {
	background: #03254e;
  }
  
  .image-container {
	background: #fff;
	color: black;
	padding: 10px;
	display: flex;
	padding: 10px;
	flex-wrap: wrap;
	height: 70vh;
	overflow: scroll;
	/* justify-content: center; */
  }
  
  .image-item {
	border: 1px solid #e8e8e8;
	border-radius: 5px;
	font-size: 30px;
	text-align: center;
	height: 220px;
	width: 220px;
	/* flex: 1 0 21%; */
	margin: 5px;
  }
  .image-item .image {
	/* background-image: url("fabric_image.png");
	background-size: cover;
	background-position: center center; */
	width: 100%;
	position: relative;
	height: 80%;
  }
  .image-item .image img {
	width: 100%;
	height: 100%;
  }
  
  .image-item .image .stain-label {
	position: absolute;
	left: 0;
	top: 10px;
	background: #0f5820;
	font-size: 20px;
	color: white;
	padding: 5px 15px;
  }
  
  .image-item .footer {
	height: 20%;
	font-family: Arial, sans-serif;
	font-size: 12px;
	color: #5f5f5f;
	font-weight: 500;
	font-weight: bold;
	display: flex;
	align-items: center;
	padding: 15px;
  }
  .image-item .footer .img-icon {
	background-image: url(data:image/png;base64,);
	width: 20px;
	height: 20px;
	margin-right: 20px;
	background-size: cover;
	background-position: center center;
  }
  
.page-item.footer {
  display: flex;
  flex-direction: column;
  padding: 0 35%;
}

.page-item.footer .start-analysis-btn,
.page-item.footer .next-upload-btn {
  background: #992b62;
  cursor: pointer;
  position: relative;
  padding: 10px 80px 10px 30px;
  border: none;
  color: #fff;
  border-radius: 30px;
  font-size: 20px;
  width: 250px;
}

.start-analysis-btn .right-arrow,
.next-upload-btn .right-arrow {
  background: #8d2858;
  display: inline-flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  top: 0;
  right: 0;
  width: 60px;
  height: 100%;
}

.page-item.footer .next-upload-btn {
  margin-top: 4.5rem;
  margin-bottom: 1rem;
  background: #012758;
  border: 1px solid #214d7a;
}
.next-upload-btn .right-arrow {
  background: #214d7a;
}

