.title {
  margin: 0 0 35px 0;
}

.page-item {
  text-align: center;
  padding: 20px;
  font-size: 30px;
}

/* upload screen / start analysis */
.upload-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 70%;
}

.upload-container .upload-item {
  margin: 15px;
}

.upload-container .file-upload,
.upload-container .batch-upload {
  border-radius: 10px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url("../../assets/photo_bg.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30%;
}

.upload-container .file-upload {
  border: 1px dashed grey;
}
.upload-container .batch-upload {
  border: 1px solid grey;
}

.upload-container .upload-btn {
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  color: white;
  background: #ed3d6c;
  font-size: 25px;
  padding: 0;
  margin-top: 1rem;
  cursor: pointer;
}
