.page-item.footer {
  display: flex;
  flex-direction: column;
  padding: 0 35%;
}

.page-item.footer .start-analysis-btn,
.page-item.footer .next-upload-btn {
  background: #992b62;
  cursor: pointer;
  position: relative;
  padding: 10px 80px 10px 30px;
  border: none;
  color: #fff;
  border-radius: 30px;
  font-size: 20px;
  width: 250px;
}

.start-analysis-btn .right-arrow,
.next-upload-btn .right-arrow {
  background: #8d2858;
  display: inline-flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  top: 0;
  right: 0;
  width: 60px;
  height: 100%;
}

.page-item.footer .next-upload-btn {
  margin-top: 4.5rem;
  margin-bottom: 1rem;
  background: #012758;
  border: 1px solid #214d7a;
}
.next-upload-btn .right-arrow {
  background: #214d7a;
}
